import React, { useState } from 'react'
import Layout from '../../components/Layouts/Layout'

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';

import toast from 'react-hot-toast';
import axios from 'axios'

import {useLocation, useNavigate} from 'react-router-dom'
import { useAuth } from '../../context/auth';
import Container from 'react-bootstrap/esm/Container';
import Card from 'react-bootstrap/Card';

const Login = () => {

       //states
       const [email,setEmail] = useState('')
       const [password,setPassword] = useState('')
       const [auth,setAuth] = useAuth()
       
   
       const navigate = useNavigate()
       const location = useLocation()

        //form handlesubmit
    const handleSubmit = async(e)=>{
        e.preventDefault()
        try {
          //sending rquest
          const res = await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/login`,{email,password})

          if(res && res.data.success){
            toast.success(res.data.message)
            setAuth({
                ...auth,
                user:res.data.user,
                token:res.data.token,
            })

            localStorage.setItem('auth',JSON.stringify(res.data))
            navigate(location.state ||"/")
          }else{
            toast.error(res.data.message)

          }
        } catch (error) {
          console.log(error)
          toast.error('Error in login')
          
        }
    }

   


  return (
    <Layout title='Login - Admin'>
      <Container className='' style={{marginTop:"100px"}}>

<Card lg={6} style={{backgroundColor:"grey"}} className='p-5 my-5'>
<h1 className='text-center text-white fw-bold'>Login page</h1>
  
<Form onSubmit={handleSubmit} >
      
       
        

        <Form.Group  md="4" controlId="validationCustom02">
          
          <Form.Control
            required
            type="email"
            placeholder="email"
            value={email}
            onChange={(e)=>setEmail(e.target.value)}
            className='my-2'
            
          />
        </Form.Group>
        <Form.Group  md="4" controlId="validationCustom02">
          
          <Form.Control
            required
            type="text"
            placeholder="Password"
            value={password}
            onChange={(e)=>setPassword(e.target.value)}
            className='my-2'
            
            />
        </Form.Group>
        

        <Button type="submit" className='btn-dark'>Submit</Button>
       
      
     
    </Form>
            </Card>

      </Container>
    </Layout>
  )
}

export default Login