import React from 'react'
import { Card, Col, Container, Row } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

const AdminMenu = () => {
  return (
    <>
    <Container style={{marginTop:"100px"}}>

       <Row >
       <Col lg={6} md={6}>
        <NavLink to="/dashboard/admin/create-category" className="nav-link ">
          <Card  className='d-flex align-items-center ' >
           <img src="/images/more.jpg" alt="cat"  className='img-fluid'/>
           <p style={{position:"absolute",top:"40%",alignItems:"center",justifyContent:"center",fontSize:"25px",fontWeight:"bolder"}} className='text-white'>
             Create category
            </p>
          </Card></NavLink>
        </Col>
      <Col lg={6} md={6}>
        <NavLink to="/dashboard/admin/create-post" className="nav-link">
          <Card  className='d-flex align-items-center justify-content-center'>
          <img src="/images/more.jpg" alt="post"   className='img-fluid'/>
          
          <p style={{position:"absolute",top:"40%",alignItems:"center",justifyContent:"center",fontSize:"25px",fontWeight:"bolder"}} className='text-white'>
             Create Post
            </p></Card>
            </NavLink>
        </Col>
     
    </Row>  
    </Container>
    </>
  )
}

export default AdminMenu