import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import Layout from "../components/Layouts/Layout";
import '../pages/Categories.css'
import { Button, Modal } from 'react-bootstrap';
import toast from "react-hot-toast";
import { useAuth } from "../context/auth";

const CategoryProduct = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState([]);
  const [category, setCategory] = useState([]);
  const [showModal, setShowModal] = useState(null);
  // const [id, setId] = useState("");
  const [show, setShow] = useState(false);
  const [auth,setAuth] = useAuth()


  const handleDeleteClose = () => setShow(false);
  const handleDeleteShow = () => setShow(true);

  const handleShow = (postId) => {
    setShowModal(postId);
  };

  const handleClose = () => {
    setShowModal(null);
  };


  useEffect(() => {
    if (params?.slug) getPrductsByCat();
    // eslint-disable-next-line
  }, []);//params?.slug

  const getPrductsByCat = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/post/post-category/${params.slug}`
      );
      setPost(data?.post);
      setCategory(data?.category);
    } catch (error) {
      console.log(error);
    }
  };


 //delete a product
 const handleDelete = async (id) => {
  try {
    // let answer = window.prompt("Are You Sure want to delete this product ? ");
    // if (!answer) return;
    const { data } = await axios.delete(
      `/api/v1/post/delete-post/${id}`
    );
    toast.success("Product Deleted Successfully");
    navigate("/");
  } catch (error) {
    console.log(error);
    toast.error("Something went wrong");
  }
};



  return (
    <Layout title='View All '>
      <div className="container-fluid  category" style={{marginTop:"90px"}}>
        <div className="d-flex align-items-center ">
          
        <h4 className="text-white fw-bold">{category?.name} </h4>
        <h6 className="text-white"> - {post?.length} posts </h6>
        </div>
            <div className="gallery">
            {post?.map((p) => (
        <div className="pics "  key={p._id} onClick={() => handleShow(p._id)}>
          

          <img
            src={`/api/v1/post/post-photo/${p._id}`}
            className="img-fluid flex-wrap"
            style={{ width: '250px', height: 'auto' }}
            alt={p.name}
          />

          
        </div>
      ))}

      {post?.map((p) => (
        <Modal
          key={p._id}
          show={showModal === p._id}
          onHide={handleClose}
          aria-labelledby={`post-modal-${p._id}`}
          className='p-3 d-flex align-items-center'
        >
          <Modal.Body  className='p-2 bg-dark rounded-4' >
        <div >
        <img 
          src={`/api/v1/post/post-photo/${p._id}`}
          alt="" className='img-fluid  rounded-5' 
        />
        <div  style={{position:"absolute",top:"5px",display:"flex",justifyContent:"space-between",alignItems:"center"}} >

       <button className="btn modal-btn m-2 rounded-4 "  onClick={() => handleClose(post._id)}>
       <i className="fa-solid fa-xmark text-dark fw-bold"></i>
        </button>

        
      

        {/* ===============delete button modal ====================== */}
        {
        !auth.user && !auth.role == 1? (
          <></>
        ):(
          <>
          <Button className="btn modal-btn m-2 rounded-4" onClick={handleDeleteShow}>
        <i className="fa-solid fa-trash" ></i>
      </Button>
          </>
        )
      }
       

      <Modal
        show={show}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
        
        
      >
        
        <Modal.Body className="bg-dark text-white border rounded">
          <h3>

         Delete Post ?
          </h3>
        </Modal.Body>
        <Modal.Footer className="bg-dark">
          <Button variant="secondary" onClick={handleDeleteClose}>
            Close
          </Button>
          <Button variant="danger" onClick={handleDelete}>Delete</Button>
        </Modal.Footer>
      </Modal>
        {/* ===============delete button modal ====================== */}

    
        </div>

        </div>
      </Modal.Body>
        </Modal>
      ))}
            </div>
           
          </div>
    </Layout>
  );
};

export default CategoryProduct;