import React, { useEffect, useState } from 'react'

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useNavigate,Link} from "react-router-dom";
import { useAuth } from '../../context/auth';

import toast from 'react-hot-toast'

const Header = () => {
  const [auth,setAuth] = useAuth()
  const navigate = useNavigate();

  const handleLogout = ()=>{
    setAuth({
      ...auth,
      user:null,
      token:""
    })
    localStorage.removeItem('auth')
    toast.success('Logout successfully')
  }


  const [pressing, setPressing] = useState(false);

  useEffect(() => {
    let pressTimer;

    const startPress = () => {
      // Set a timeout for 5 seconds (5000 milliseconds)
      pressTimer = setTimeout(() => {
        // Perform the redirect when the timer expires
        navigate('/login')
      }, 5000);
      setPressing(true);
    };

    const cancelPress = () => {
      // Clear the timeout if the press is released before 5 seconds
      clearTimeout(pressTimer);
      setPressing(false);
    };

    // Attach the events directly to the brand name element
    const brandNameElement = document.getElementById('brandName'); // Replace 'brandName' with the actual ID of your brand name element

    if (brandNameElement) {
      brandNameElement.addEventListener('mousedown', startPress);
      brandNameElement.addEventListener('touchstart', startPress);

      brandNameElement.addEventListener('mouseup', cancelPress);
      brandNameElement.addEventListener('touchend', cancelPress);
    }

    // Clean up event listeners on component unmount
    return () => {
      if (brandNameElement) {
        brandNameElement.removeEventListener('mousedown', startPress);
        brandNameElement.removeEventListener('touchstart', startPress);
        brandNameElement.removeEventListener('mouseup', cancelPress);
        brandNameElement.removeEventListener('touchend', cancelPress);
      }
    };
  }, []);


  return (
    <>
    
    <Navbar expand="lg" className="navbar fixed-top">
      <Container fluid>
        {/* <Navbar.Brand href="#" className='text-white fs-6'>M_JOHNZY</Navbar.Brand> */}
        <div>
      <span
        id="brandName"
        style={{ cursor: 'pointer', fontWeight: 'bold', fontSize: '1.2em' }}
      >
        M_JOHNZY
      </span>
      {pressing}
    </div>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav className=" ms-auto my-2 my-lg-0">
            <Link href="#" className="nav-link text-white" to='/'>Home</Link>
      {
        !auth.user ? (
          <>
        </>
        ):(
          <>
            <Link href="#" onClick={handleLogout} className="nav-link text-white" to='/login'>Logout</Link>
          </>
        )
      }
            
           
          </Nav>
          
        </Navbar.Collapse>
      </Container>
    </Navbar></>
  )
}

export default Header