import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layouts/Layout'
import axios from 'axios'
import toast from 'react-hot-toast'
import {Select} from 'antd'
import { useNavigate } from 'react-router-dom'
import { Col, Container, Row } from 'react-bootstrap'

const {Option} = Select

const CreatePost = () => {
  const [categories,setCategories] = useState([])
  const [name,setName] = useState("")
  const [category,setCategory] = useState("")
  const [photo,setPhoto] = useState("")

  const navigate = useNavigate()

   //get all post
   const getAllCategory = async()=>{
    try {
        const {data} = await axios.get('/api/v1/category/get-category')
        if(data?.success){
            setCategories(data?.category)
        }
    } catch (error) {
        console.log(error)
        toast.error('somethinf went wrong in getting category')
    }
}
useEffect(()=>{
  getAllCategory()
},[])

//upload post function
const handleUpload = async(e)=>{
  e.preventDefault()
  try {
    const postData = new FormData()
    postData.append('name',name)
    postData.append('photo',photo)
    postData.append('category',category)
    if(!name||!photo||!category){
toast.error("enter all fields")
    }else{

      const {data} = axios.post('/api/v1/post/create-post',postData)
      if(data?.success){
        
        toast.error(data?.message)
        
      }else{
        toast.success("uploaded")
        navigate(`/`)
      }
    }
  } catch (error) {
    console.log(error)
    toast.error("Error in uploading post")
    
  }
}

const handleReset = ()=>{
  setName('')
  setPhoto(0)
}
  return (
    <Layout title={"Create Post"}>
    <div className=" p-2">
      <Container className='p-4 mb-5' style={{marginTop:"100px"}}>
      <h2 className='text-white  fw-bold '>Create Post</h2>
<Row className='py-3 my-5'>
        <Col >
      {/* image preview */}
      <div className="mb-3 ">
        
        {photo?  (
          <div className="text-center ">
            <img src={URL.createObjectURL(photo)} alt="product-photo" height={'200px'} className='img img-responsive'/>
          </div>
        ):(
          <div className='d-flex align-items-center justify-content-center '>

          <label 
          className='btn'>
           {photo? photo.name : <i className="fa-solid fa-image" style={{color: "#ffffff",fontSize:"100px"}}></i>}
           <input type="file" name="photo" accept='image/*' onChange={(e)=>setPhoto(e.target.files[0])} hidden/>
         </label>
          </div>
        )}
      </div>
        </Col>
  <Col>

      <div className="mb-3">
        <input type="text" value={name} placeholder='write a name' className='form-control' onChange={(e)=>setName(e.target.value)} />
      </div>
      <Select bordered={false} placeholder="Select a category" size='large' showSearch className='form-select mb-3' onChange={(value)=>{setCategory(value)}}>
        {categories?.map(cat=>(
          <Option key={cat._id} value={cat._id}>{cat.name}</Option>
          ))}

      </Select>

       <div className='d-flex gap-2'>
      <button className='btn btn-warning' onClick={handleUpload}>Upload</button>
      <button className='btn btn-warning' onClick={handleReset}>reset</button>
       </div>


      
        </Col>
           </Row>
        </Container>
    </div>
    </Layout>
  )
}

export default CreatePost