import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layouts/Layout'
import toast from 'react-hot-toast'
import Table from 'react-bootstrap/Table';
import axios from 'axios';
import CategoryForm from '../../components/Form/CategoryForm';
import {Modal} from 'antd'
import Card from 'antd/es/card/Card';
const CreateCategory = () => {

    const [categories,setCategories] = useState([])
    const [name,setName] = useState("")
    const [visible,setVisible] = useState(false)
    const [selected,setSelected] = useState(null)
    const [updatedName,setUpdatedName] = useState("")

    //handle form
    const handleSubmit = async(e)=>{
        e.preventDefault()
        try {
            const {data} = await axios.post('/api/v1/category/create-category',{name})
            if(data?.success){
                toast.success(`${name} is created`)
                getAllCategory()
            }else{
                toast.error(data.message)
            }
        } catch (error) {
            console.log(error)
            toast.error("Something went wrong in input form")
            
        }
    }

    //update category
    const handleUpdate = async(e)=>{
        e.preventDefault()
        try {
           const {data} = await axios.put(`/api/v1/category/update-category/${selected._id}`,{name:updatedName})
           if(data.success){
            toast.success(`${updatedName} is Updated`)
            setSelected(null)
            setUpdatedName("")
            setVisible(false)
            getAllCategory();
           }else{
            toast.error(data.message)
           }
        } catch (error) {
            console.log(error)
            toast.error("went wrong in updating category name")
            
        }
    }
       //delete category
       const handleDelete = async(pId)=>{
        try {
           const {data} = await axios.delete(`/api/v1/category/delete-category/${pId}`)
           if(data.success){
            toast.success(`Category is deleted`)
            getAllCategory();
           }else{
            toast.error(data.message)
           }
        } catch (error) {
            console.log(error)
            toast.error("went wrong in updating category name")
            
        }
    }


    //get all categories
    const getAllCategory = async()=>{
        try {
            const {data} = await axios.get('/api/v1/category/get-category')
            if(data?.success){
                setCategories(data?.category)
            }
        } catch (error) {
            console.log(error)
            toast.error('somethinf went wrong in getting category')
        }
    }

    useEffect(()=>{
        getAllCategory()
    },[])
  return (
    <Layout title='Create Category'>
        <div className="container p-3 " style={{marginTop:"100px"}}>
            <h2 className='text-white'>Create Category</h2>
            <div>
                <CategoryForm handleSubmit={handleSubmit} value={name} setValue={setName}/>
            </div>
            <Card className='bg-dark border mt-4'>

        <Table   size="lg" className='text-white my-4 '>
        
      <thead>
        <tr className='text-center'>
          
          <th>Name</th>
          <th >Actions</th>
        </tr>
      </thead>
      <tbody>
         {categories?.map(cat=>(
             <>
        <tr className='text-center'>
            <td key={cat._id}>{cat.name}</td>
            <td><button className='btn btn-primary ms-3' onClick={()=>{setVisible(true);setUpdatedName(cat.name); setSelected(cat)}}><i className="fa-solid fa-pen-to-square" style={{color: '#d1e2ff'}}></i></button>
            <button className='btn btn-danger ms-3' onClick={()=>{handleDelete(cat._id)}}><i className="fa-solid fa-trash" style={{color: "#ffb8bf"}}></i></button></td>
        </tr>
            </>
         ))}
        
      </tbody>
    </Table>
         </Card>
        </div>
        <Modal onCancel={()=>setVisible(false)} footer={null} open={visible}>
            <CategoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={handleUpdate}/>
        </Modal>

    </Layout>
  )
}

export default CreateCategory